.container {
  justify-content: space-between;
  width: 100%;
}

@media screen and (max-width: 687px) {
  .container {
    align-items: center;
    justify-content: space-around !important;
  }
}

.brand-logo {
  height: 25px;
  margin-right: 15px;
}

.header-left {
  align-items: center;
  cursor: default;
  display: flex;
  font-weight: bold;
  font-size: 14px;
  text-decoration: none;
}

.header-left:hover {
  text-decoration: none;
}

.header-right {
  align-items: center;
  color: var(--black);
  display: flex;
  flex-direction: row;
  font-weight: bold;
  font-size: 14px;
}

.header-right div {
  margin-left: 20px;
}

.header-right-item {
  border-right: 1px solid var(--black);
  cursor: pointer;
  display: flex;
  flex-direction: row;
  padding: 5px 20px;
  text-decoration: none;
  transition: all 500ms ease-in-out;
}

.header-right-item:hover {
  text-decoration: none;
}

.header-right-item img {
  height: 20px;
}

.header-right-item div {
  margin-left: 7px;
}

nav.navbar {
  background-color: var(--dark-gray);
  border-bottom: 1px solid var(--black);
  -webkit-box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.75);
  height: 30px;
  padding: 0;
}
