.App {
    background-size: cover;
    background-position: center;
    display: flex;
    flex-direction: column;
    height: 100%;
    text-align: center;
    transition: all 2s ease-in-out;
    animation: fadeIn 1s ease-in-out forwards;
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: .9;
    }
}

.show {
    display: block;
}

.modal-dialog {
    margin: 0;
    max-width: inherit;
    height: 100%;
}

.modal-content {
    background: rgba(0, 0, 0, 0.8);
    color: #fff;
    height: 100%;
}

.modal-inner {
    margin: 1.75rem auto;
    max-width: 500px;
    position: relative;
    width: auto;
}

.images-container {
    display: flex;
    flex-direction: row;
    overflow: scroll;
}

.modal-image {
    max-height: 300px;
    margin-right: 20px;
}

.title-wrapper {
    display: flex;
    justify-content: space-between;
}

.title-wrapper .heading {
    margin-bottom: 0;
}

.modal-primary-logo {
    max-height: 50px;
}

.wrapper {
    margin-bottom: 20px;
}

.sub-title {
    font-weight: bold;
    margin-bottom: 15px;
    text-align: left;
    text-transform: uppercase;
    letter-spacing: .2rem;
}

.text-content {
    margin-top: 15px;
    text-align: left;
}

/**
 * Custom Scrollbar
 * ref.: https://www.w3schools.com/howto/howto_css_custom_scrollbar.asp
 */

/* width */
::-webkit-scrollbar {
    width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
    background: var(--white);
}

/* Handle */
::-webkit-scrollbar-thumb {
    border-radius: 50px;
    background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555;
}
